*{margin:0; padding:0; box-sizing: border-box; list-style-type:none;}
button:focus, button:focus, button:click{outline:none; border:0;}
body{ font-family:verdana, Arial, Helvetica, sans-serif;}
/*font color*/
$fontblue:#fc3d3d;
$fontDark:#2d2e30;
$fontgreen:#1c9816;
$fontWhite:#ffffff;
$fontDGry:#717171;

/*bg color*/
$bgblue:#4269ea;
$fbggrey:#f2f3f7;
$fbgdarkyel:#f7ae26;

/*margin*/
$marginLRAuto:0 auto;
$wrapmaxwid:1200px;
$botTop10: 10px 0;

@mixin centerWrap{margin:$marginLRAuto; max-width:$wrapmaxwid; padding:$botTop10;}
@mixin dFlex{display:flex; flex-wrap:wrap; box-sizing:border-box; }

/*css for header */
.header, footer{ background:$bgblue; 
	.contnar{@include centerWrap; display:flex; text-align:left;flex-wrap: wrap; color:$fontWhite;
	.star{font-size:25px; color:$fbgdarkyel; }
	.headerRig{flex-grow:1;display:flex; justify-content: flex-end;
	svg{font-size:22px; color:$fontWhite; margin-left:10px;}
	}
	}
}
.searchCont{display:flex;
	input[type="text"]{ padding:5px 4px; border:1px solid #ccc;}
	button{background:transparent; border:0;}
}

/*css for footer */
footer{ .contnar{justify-content: center; }}

/*css for midSection */
.midSect{background:$fbggrey; 
.contnar{@include centerWrap; display:flex; text-align:left; color:$fontDGry;
	.leftSect{flex-basis:180px; flex-shrink:0;}
	.rigSect{flex-grow:1; padding-left:10px; margin-right:10px; border-left: 1px solid #ddd;
		.itemContner {
			@include dFlex;
				li{flex: 1 0 20%; margin:5px; background:#fff; padding:5px; box-sizing:border-box;
				img{width:100%;}
				h2{font-size:12px;}
				h3{font-size:14px; padding:2px 0;
					.priDisplay{color:#ccc; text-decoration: line-through; font-size:13px;}
					.priRecord{color:$fontgreen;}
				}
				button{border: 1px solid #ccc; padding:7px 8px;border-radius:5px; display: block; margin:10px auto 5px;font-size:12px; text-align:center; color:$fontDark; background:$fbgdarkyel;}
			}
		}
	}
	}
}